.boxi-nosotros {
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 3% 20%;

}

.container-profile-nos {
  width: 240px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-name {
  margin-top: 2vh;
  text-align: center;
  color: #6667AB;
  font-size: 28px;
  font-weight: 100;
}

.profile-cargo {
  text-align: center;
  color: #737373;
  font-size: 22px;
  font-weight: 100;

}

.profile-photo-nos {
  width: 220px;
  height: 220px;
  border-radius: 220px;
  border-style: solid;
  border-width: 10px;
  border-color: #ffffff;

}

.profile-photo-nos img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 220px;

}



















